<template>
  <div class="Daily">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="80px" class="ruleForm">
        <el-form-item label="" prop="days" required>
          <el-input v-model="ruleForm.days" autocomplete="off" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="ruleForm.contain">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item>
          <el-button size="mini" type="primary" @click="submitForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Daily",
  components: {},
  created() { },
  watch: {},
  data() {
    var validateDays = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    return {
      id: this.$route.query.id,

      ruleForm: {
        days: "",
        contain: "",
      },
      rules: {
        days: [{ validator: validateDays, trigger: "blur" }],
      },
    };
  },

  methods: {
    /**
     * 
     */
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.request();
        } else {
          this.$message.error("");
          return false;
        }
      });
    },
    request() {
      const pro = { //
        "days": this.ruleForm.days, //
        "alsoEvents": this.ruleForm.contain == "" ? false :true, //
        "doneDays": 0, //
        "giftPoolCount": 0, //
        "donePoolCount": 0, //
        "status": "N", //  N D E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long 
        "lastUpdateTime": ""
      }
      const params = {
        orderId: this.id,
        boostingTask: "=" + this.ruleForm.days +
          '|=' + this.ruleForm.contain + '|' + "",
        progress: JSON.stringify(pro),
        target: this.ruleForm.days,

        operateType: "ADD",
        businessType: "Daily"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            if(body.message == null || body.message == "") {
              this.$message.success("");
            } else {
              this.$message.success(body.message);
            }
          } else {
            this.$message.error(body.message);
          }
        }

      );
    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}

.content {
  padding: 0px 100px 0 20px;

}
</style>
  