          <el-checkbox label="" v-model="addOrderForm.teamOnHold"></el-checkbox>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.boostingContent" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.orderAmount" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.playerContact" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddFormVisible = false"></el-button>
        <el-button type="primary" @click="handleAddOrder"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" width="500px" :visible.sync="dialogModifyFormVisible">
      <el-form :model="addOrderForm">

        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.orderNumber" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" required>
          <el-input v-model="addOrderForm.playerAccount" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" required>
          <el-input v-model="addOrderForm.playerEncryptionCode" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="addOrderForm.playerChannel" placeholder="" width="100%">
            <el-option v-for="item in playerChannelOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="AP" :label-width="formLabelWidth">
          <el-checkbox label="" v-model="addOrderForm.fruitEnabled"></el-checkbox>
          <el-checkbox label="" v-model="addOrderForm.rockEnabled"></el-checkbox>
          <span></span>

        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-checkbox label="" v-model="addOrderForm.teamOnHold"></el-checkbox>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.boostingContent" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.orderAmount" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.playerContact" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogModifyFormVisible = false"></el-button>
        <el-button type="primary" @click="handleModifyOrder"></el-button>
      </div>
    </el-dialog>
    <div class="header">
      <span class="logo">FGO</span>
      <span class="logout" @click="handleLogout"><i class="el-icon-user"></i> {{ username }}</span>
    </div>
    <div class="content">
      <div class="aside">
        <el-menu :default-active="activeNavName" class="el-menu-vertical" background-color="#545c64" text-color="#fff"
          active-text-color="#ffd04b">
          <el-submenu index="1">
            <template slot="title">
              <span></span>
            </template>
            <el-menu-item v-for="(item, i) in navList" :key="i" :index="item.name" @click="selectNav(item.name)">
              {{ item.navItem }}
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
      <div class="content-right">
        
        <div class="content-header">
          <!--  -->
          <div class="settle-option">
            <el-button size="mini" type="primary" icon="el-icon-minus" @click="handleMidSettle"></el-button>
            <el-button size="mini" type="primary" icon="el-icon-check" @click="handleSubmitSettle"></el-button>
            <el-button size="mini" type="primary" icon="el-icon-upload2" @click="handleOrderUpload"></el-button>
            <el-button size="mini" type="primary" icon="el-icon-download" @click="handleOrderDownload"></el-button>
          </div>
          <!--  -->
          <div class="search">
            <el-input class="search-input" v-model="searchValue" size="medium" placeholder="" />
            <el-button size="small" class="search-button" type="primary" icon="el-icon-search"
              @click="handleSearchOrder"></el-button>
          </div>
          <!--  -->
          <div class="select-status">
            <span class="select-label"></span>
            <el-select size="medium" class="select-status-input" v-model="activeNavName" placeholder="">
              <el-option v-for="item in selectStatusOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <!--  -->
          <div class="order-option">
            <el-button size="small" type="primary" icon="el-icon-plus" @click="handleOrderAddDialog"></el-button>
            
          </div>


        </div>
        <div class="content-inner">
          <!--  -->
          <div class="OrderList">
            <el-table stripe border size="mini" header-align="center" :data="tableData" cell-class-name="table_cell"
              style="width: 100%;" :max-height="tableMaxHeight" @selection-change="handleSelectionChange"
              @sort-change="handleTableSort">
              <el-table-column align="center" fixed="left" type="selection" width="40">
              </el-table-column>
              <!-- <el-table-column fixed="left" prop="id" label="id">
              </el-table-column> -->
              <el-table-column align="center" show-overflow-tooltip fixed="left" prop="orderNumber" label="">
              </el-table-column>

              <el-table-column align="center" show-overflow-tooltip fixed="left" prop="playerAccount" label="" width="100">
              </el-table-column>
              <el-table-column align="center" show-overflow-tooltip prop="playerEncryptionCode" label="">
              </el-table-column>
              <el-table-column align="center" show-overflow-tooltip prop="playerChannel" label="" width="50"
                :formatter="playerChannelFormatter">
              </el-table-column>
              <el-table-column align="center" show-overflow-tooltip sortable="custom" prop="createTime" label=""
                width="120" :formatter="createTimeFormatter">
              </el-table-column>
              <el-table-column align="center" show-overflow-tooltip sortable="custom" prop="recentlyLoginTime" label=""
                width="120" :formatter="recentlyLoginTimeFormatter">
              </el-table-column>
             
              <el-table-column align="center" show-overflow-tooltip prop="rockFruitAtStart" label="">
              </el-table-column>

              <el-table-column align="center" show-overflow-tooltip prop="rockFruitAtCurrent" label="">
              </el-table-column>

              <el-table-column align="center" show-overflow-tooltip prop="battelCount" label="">
              </el-table-column>

              <el-table-column align="center" show-overflow-tooltip prop="actionPowerRecovery" label="AP" width="90">
              </el-table-column>
  
              <el-table-column align="center" show-overflow-tooltip prop="status" label=""
                :formatter="statusFormatter">
              </el-table-column>
              <el-table-column align="center" show-overflow-tooltip prop="orderAmount" label="" width="50">
              </el-table-column>

              <el-table-column align="center" show-overflow-tooltip prop="orderAmount" label="" width="60">
              </el-table-column>
              <el-table-column align="center" show-overflow-tooltip prop="playerContact" label="">
              </el-table-column>
        
              <el-table-column fixed="right" label="" width="220">
                <template slot-scope="scope">
                  <!-- <el-tooltip :content="scope.row.status" placement="top">
                    <el-switch class="switch-button" v-model="scope.row.status" active-color="#13ce66"
                      inactive-color="#ff4949" active-value="RUNNING" inactive-value="STOPPED"
                      @change="handleChangeOrderStatus(scope.row)">
                    </el-switch>
                  </el-tooltip> -->
                  <el-button v-if="scope.row.status != 'RUNNING'" size="mini" type="primary"
                    @click="handleStartBusiness(scope.row)">
                  </el-button>
                  <el-button v-if="scope.row.status == 'RUNNING'" size="mini" type="primary"
                    @click="handleStopBusiness(scope.row)">
                  </el-button>

                  <el-button size="mini" type="primary" @click="handleJumpBusiness(scope.row)">
                  </el-button>
                  <el-button size="mini" type="primary" @click="handleOrderModifyDialog(scope.row)">
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="content-footer">
          <!--  -->
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="orderListParams.pageNum" :page-sizes="[10, 20, 30, 40]" :page-size="orderListParams.pageSize"
            layout="total, sizes, prev, pager, next, jumper" :total="paginationInfo.total">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Order",
  components: {},
  created() {
    if (window.innerHeight < 900) {
      this.tableMaxHeight = "500px";
    } else if (window.innerHeight >= 900) {
      this.tableMaxHeight = "700px";
    }
    this.navList.forEach((item) => {
      this.selectStatusOptions.push({
        value: item.name,
        label: item.navItem,
      });
    });
    this.username = this.$store.getters.getUserName;
    this.account = this.$store.getters.getAccount;

  },
  watch: {
    activeNavName: {
      handler(newVal) {
        this.updateOrderListByStatus(newVal);
      },
      immediate: true,
    },
  },
  data() {
    return {
      username: "",
      account: "",
      /**
       * const 
       */
      navList: [
        { name: "", navItem: "" },

        { name: "UNFINISHED", navItem: "" },
        { name: "UNFINIISHED_EVENT_DONE", navItem: "" },
        { name: "ABNORMAL", navItem: "" },
        { name: "PASSWORD_EXCEPTION", navItem: "" },
        { name: "OUT_OF_ACTIVE_POWER", navItem: "AP" },
        { name: "FINISHED", navItem: "" },
        { name: "SETTLED", navItem: "" },
        { name: "BLACK_LIST", navItem: "" },
      ],
      statusMap: {
        /**
 * 
 */
        INIT: "",

        /**
         * 
         */
        RUNNING: "",

        /**
         * 
         */
        STOPPED: "",

        /**
         * 
         */
        FINISHED: "",
        /**
         * 
         */
        UNFINISHED: "",
        /**
         * 
         */
        UNFINISHED_EVENT_DONE: "",
        /**
         * 
         */
        ABNORMAL: "",
        /**
         * 
         */
        PASSWORD_EXCEPTION: "",
        /**
         * 
         */
        OUT_OF_ACTIVE_POWER: "",
        /**
         * 
         */
        BLACK_LIST: "",
        /**
         * 
         */
        SETTLED: "",
      },
      playerChannelMap: {
        iOS: "",
        Android: "",
      },
      /**
       * const 
       */
      selectStatusOptions: [

      ],
      /**
       * 
       */
      playerChannelOptions: [
        {
          value: "Android",
          label: "",
        }, {
          value: "iOS",
          label: "",
        },
      ],
      /**
       * var 
       */
      activeNavName: "",
      tableMaxHeight: "",
      tableHeader: [
        // // {
        // //   key: "playerAccount",
        // //   name: "",
        // // },
        // {
        //   key: "playerEncryptionCode",
        //   name: "",
        // },
        // {
        //   key: "playerChannel",
        //   name: "",
        // },
        // // {
        // //   key: "createTime",
        // //   name: "",
        // // },
       
        // {
        //   key: "boostingContent",
        //   name: "",
        // },
        // {
        //   key: "rockFruitAtStart",
        //   name: "",
        // },
        // {
        //   key: "rockFruitAtCurrent",
        //   name: "",
        // },
        // {
        //   key: "battelCount",
        //   name: "",
        // },
        // {
        //   key: "actionPowerRecovery",
        //   name: "AP",
        // },
        // // {
        // //   key: "teamOnHold",
        // //   name: "",
        // // },
        // {
        //   key: "orderAmount",
        //   name: "",
        // },
        // {
        //   key: "orderAmount",
        //   name: "",
        // },
        // {
        //   key: "playerContact",
        //   name: "",
        // },
      ],
      /**
       * var 
       */
      searchValue: "",
      /**
       * var 
       */
      paginationInfo: {
        total: 0,
        totalPage: 0,
      },

      /**
       * var 
       */
      orderListParams: {
        orderNumber: "",
        status: "", // UNFINISHED,UNFINIISHED_EVENT_DONE,ABNORMAL,PASSWORD_EXCEPTION,OUT_OF_ACTIVE_POWER,FINISHED,BLACK_LIST,SETTLED
        pageNum: 1,
        queryStr: "",
        orderBy: "ASC",
        pageSize: 20,
      },
      /**
       * 
       */
      dialogAddFormVisible: false,
      formLabelWidth: "120px",
      addOrderForm: {
        id: "",
        orderNumber: "",
        playerAccount: "",
        playerEncryptionCode: "",
        playerChannel: "0",
        fruitEnabled: false,
        rockEnabled: false,
        teamOnHold: "",
        boostingContent: " ",
        orderAmount: "",
        playerContact: "",
      },
      /**
       * 
       */
      dialogModifyFormVisible: false,

      /**
       * var 
       */
      tableData: [

      ],
      /**
       * 
       */
      multipleSelection: [],
    };
  },

  methods: {
    /**
     * 
     */
    selectNav(index) {
      this.activeNavName = index;
    },

    /**
     * 
     */
    jumpUrl(path) {
      this.$router.push(path);
    },
    statusFormatter(row) {
      return this.statusMap[row.status];
    },
    playerChannelFormatter(row) {
      return this.playerChannelMap[row.playerChannel];
    },
    createTimeFormatter(row) {
      if(row.createTime == null) {
        return ;
      }
      let date = row.createTime.split("T");
      return date[0] + " " + date[1].split(".")[0].slice(0, 5);
    },
    recentlyLoginTimeFormatter(row) {
      if(row.recentlyLoginTime == null) {
        return ;
      }
      let date = row.recentlyLoginTime.split("T");
      return date[0] + " " + date[1].split(".")[0].slice(0, 5);
    },
    
    // 
    /**
     * 1. 
     */
    updateOrderList() {
      this.$api.getOrder(this.orderListParams).then((res) => {
        const body = res.data;
        if (body.code == "200") {
          let data = body.data;
          this.tableData = [];

          for (let i = 0; i < data.list.length; i++) {
            let item = data.list[i];
            let listItem = {
              id: item.id,
              orderNumber: item.orderNumber,
              playerAccount: item.playerAccount,
              playerEncryptionCode: item.playerEncryptionCode,
              playerChannel: item.playerChannel,
              actionPowerRecovery: "",
              teamOnHold: this.addOrderForm.teamOnHold,
              boostingContent: item.boostingContent,
              orderAmount: item.orderAmount,
              playerContact: item.playerContact,
              status: item.status,
              createTime: item.createTime,
              recentlyLoginTime: item.recentlyLoginTime,
              rockFruitAtStart: item.fruitAtStart,
              rockFruitAtCurrent: item.fruitAtCurrent,
              battelCount: item.battleCount,
              exceptionMessage: item.exceptionMessage,
            };
            if (item.fruitEnabled == "TRUE") {
              listItem.actionPowerRecovery += " ";
            }
            if (item.rockEnabled == "TRUE") {
              listItem.actionPowerRecovery += " ";
            }
            if (item.fruitEnabled == "FALSE" && item.rockEnabled == "FALSE") {
              listItem.actionPowerRecovery = "";
            }
            if (item.teamOnHold == "TRUE") {
              listItem.teamOnHold = "";
            } else if (item.teamOnHold == "FALSE") {
              listItem.teamOnHold = "";
            }
            this.tableData.push(listItem);
          }
          this.paginationInfo = {
            total: data.total,
            totalPage: data.pages,
          }

        } else {
          this.$message.error(body.message);
        }
      });
    },
    // 
    updateOrderListByStatus(status) {
      this.orderListParams.pageNum = 1;
      this.orderListParams.status = status;
      this.updateOrderList();
    },

    /**
     * 2. 
     */

    /**
     * 3. 
     */
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleMidSettle() {
      let param = [];
      this.multipleSelection.forEach(item => {
        param.push(item.id);
      })
      this.$api.midSettle(param).then((res) => {
        const body = res.data;
        if (body.code == "200") {
          if (body.message == null || body.message == "") {
            this.$message.success("");
            this.updateOrderList();

          } else {
            this.$message.success(body.message);
          }
        } else {
          this.$message.error(body.message);
        }
      });
    },
    /**
     * 4. 
     */
    handleSubmitSettle() {
      let param = [];
      this.multipleSelection.forEach(item => {
        param.push(item.id);
      })
      this.$api.midSettle(param).then((res) => {
        const body = res.data;
        if (body.code == "200") {
          if (body.message == null || body.message == "") {
            this.$message.success("");
            this.updateOrderList();

          } else {
            this.$message.success(body.message);
          }
        } else {
          this.$message.error(body.message);
        }
      });
    },
    /**
     * 5. 
     */
    handleOrderAddDialog() {
      this.clearAddOrder();
      this.dialogAddFormVisible = true;
    },
    handleAddOrder() {
      let params = {
        orderNumber: this.addOrderForm.orderNumber,
        playerAccount: this.addOrderForm.playerAccount,
        playerEncryptionCode: this.addOrderForm.playerEncryptionCode,
        playerChannel: this.addOrderForm.playerChannel,
        fruitEnabled: this.addOrderForm.fruitEnabled ? "TRUE" : "FALSE",
        rockEnabled: this.addOrderForm.rockEnabled ? "TRUE" : "FALSE",
        teamOnHold: this.addOrderForm.teamOnHold ? "FALSE" : "TRUE",
        boostingContent: this.addOrderForm.boostingContent,
        orderAmount: this.addOrderForm.orderAmount,
        playerContact: this.addOrderForm.playerContact,
      }
      this.$api.addOrder(params).then((res) => {
        const body = res.data;

        if (body.code == "200") {
          if (body.message == null || body.message == "") {
            this.$message.success("");
          } else {
            this.$message.success(body.message);
          }
          this.dialogAddFormVisible = false;
          this.clearAddOrder();
          this.updateOrderList();
        } else {
          this.$message.error(body.message);
          this.clearAddOrder();
        }
      });
    },
    clearAddOrder() {
      this.addOrderForm = {
        id: "",
        orderNumber: "",
        playerAccount: "",
        playerEncryptionCode: "",
        playerChannel: "",
        fruitEnabled: false,
        rockEnabled: false,
        teamOnHold: "",
        boostingContent: " ",
        orderAmount: "",
        playerContact: "",
      };
    },
    /**
     * 9. 
     */
    handleOrderModifyDialog(row) {
      this.dialogModifyFormVisible = true;
      this.addOrderForm = {
        id: row.id,
        orderNumber: row.orderNumber,
        playerAccount: row.playerAccount,
        playerEncryptionCode: row.playerEncryptionCode,
        playerChannel: row.playerChannel,
        fruitEnabled: "",
        rockEnabled: "",
        teamOnHold: row.teamOnHold == "" ? true : false,
        boostingContent: row.boostingContent,
        orderAmount: row.orderAmount,
        playerContact: row.playerContact,
      };
      const ap = row.actionPowerRecovery.split(" ");
      for (let i = 0; i < ap.length; i++) {
        if (ap[i] == "") continue;
        if (ap[i] == "") {
          this.addOrderForm.fruitEnabled = true;
        } else if (ap[i] == "") {
          this.addOrderForm.rockEnabled = true;
        } else {
          this.addOrderForm.rockEnabled = false;
          this.addOrderForm.fruitEnabled = false;
        }
      }
    },
    handleModifyOrder() {

      let params = {
        id: this.addOrderForm.id,
        orderNumber: this.addOrderForm.orderNumber,
        playerAccount: this.addOrderForm.playerAccount,
        playerEncryptionCode: this.addOrderForm.playerEncryptionCode,
        playerChannel: this.addOrderForm.playerChannel,
        fruitEnabled: this.addOrderForm.fruitEnabled ? "TRUE" : "FALSE",
        rockEnabled: this.addOrderForm.rockEnabled ? "TRUE" : "FALSE",
        teamOnHold: this.addOrderForm.teamOnHold ? "FALSE" : "TRUE",
        boostingContent: this.addOrderForm.boostingContent,
        orderAmount: this.addOrderForm.orderAmount,
        playerContact: this.addOrderForm.playerContact,
      };

      this.$api.modifyOrder(params).then((res) => {
        const body = res.data;

        if (body.code == "200") {
          if (body.message == null || body.message == "") {
            this.$message.success("");
          } else {
            this.$message.success(body.message);
          }
          this.dialogModifyFormVisible = false;
          this.clearAddOrder();
          this.updateOrderList();
        } else {
          this.$message.error(body.message);
          this.clearAddOrder();
        }
      });

    },

    /**
     * 6. 
     */
    handleSearchOrder() {
      this.orderListParams.queryStr = this.searchValue;
      this.orderListParams.pageNum = 1;
      this.orderListParams.pageSize = 20;
      this.updateOrderList();

    },
    /**
     * 7. 
     */
    handleChangeOrderStatus(row) {
      const params = {
        orderId: row.id,
        orderStatus: row.status,
      };
      this.$api.changeOrderStatus(params).then((res) => {
        const body = res.data;
        this.updateOrderList();

        if (body.code == "200") {
          if (body.message == null || body.message == "") {
            this.$message.success("");
          } else {
            this.$message.success(body.message);
          }
        } else {
          row.status = "STOPPED";
          this.$message.error(body.message);
        }
      });

    },
    handleStartBusiness(row) {
      const params = {
        orderId: row.id,
        orderStatus: "RUNNING",
      };

      this.$api.changeOrderStatus(params).then((res) => {
        const body = res.data;
        this.updateOrderList();

        if (body.code == "200") {
          if (body.message == null || body.message == "") {
            this.$message.success("");
          } else {
            this.$message.success(body.message);
          }
        } else {
          this.$message.error(body.message);
        }
      });

    },
    handleStopBusiness(row) {
      const params = {
        orderId: row.id,
        orderStatus: "STOPPED"
      };
      this.$api.changeOrderStatus(params).then((res) => {
        const body = res.data;
        this.updateOrderList();

        if (body.code == "200") {
          if (body.message == null || body.message == "") {
            this.$message.success("");
          } else {
            this.$message.success(body.message);
          }
        } else {
          this.$message.error(body.message);
        }
      });

    },
    /**
     * 8. 
     */
    // 
    handleJumpBusiness(row) {
      const routeData = this.$router.resolve({
        path: '/task',
        query: {
          id: row.id
        }
      });
      window.open(routeData.href, '_blank');
    },
    /**
     * 10. 
     */
    handleSizeChange(val) {
      this.orderListParams.pageSize = val;
      this.updateOrderList();
    },
    handleCurrentChange(val) {
      this.orderListParams.pageNum = val;

      this.updateOrderList();

    },


    /**
     * 
     */
    handleTableSort(e) {
      if (e.order == "ascending") {
        this.orderListParams.orderBy = "ASC";
      } else {
        this.orderListParams.orderBy = "DESC";
      }
      this.updateOrderList();
    },
    handleLogout() {
      const params = {
        account: this.account,
      };
      this.$api.logout(params).then((res) => {
        const body = res.data;

        if (body.code == "200") {
          this.$router.push('/login');
          this.$store.commit('delToken');
        } else {
          this.$message.error(body.message);
        }
      });
    },
    /**
     * 
     */
    handleOrderUpload() {

    },
    /**
     * 
     */
    handleOrderDownload() {

    },


  },
};
</script>

