  methods: {
    /**
     * 
     */
    submitForm() {
      this.request();
    },
    request() {
      const pro = [{
        "levelName": this.ruleForm.level,
        "boostingContent": this.ruleForm.content,
        "status": "N"
      }]
      const params = {
        orderId: this.id,
        boostingTask: "=" + this.ruleForm.level +
          '|=' + this.ruleForm.content +
          "|",
        operateType: "ADD",
        progress: JSON.stringify(pro),
        target: this.ruleForm.content,

        businessType: "PurchaseLevels"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            if(body.message == null || body.message == "") {
              this.$message.success("");
            } else {
              this.$message.success(body.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }

      );
    },

  }
};
