


<template>
  <div class="Material">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span></span>
          <el-button size="mini" style="margin-left:10px;" type="primary" @click="handlePM"></el-button>
        </div>
        <div class="card-content">
          <table>
            <tr v-for="(item, index) in data" :key="index">
              <td class="tableHeader">
                <el-checkbox v-model="checkAll[index]" @change="handleCheckAllChange(index)">{{
                    item.children[0].label
                }}
                </el-checkbox>

              </td>
              <td class="tableContent">
                <el-checkbox-group v-model="checkedList[index]">
                  <el-checkbox v-for="(jtem, i) in item.children[0].children" :label="i" :key="jtem.label">
                    {{ jtem.label }}
                    <input class="tableInput" type="text" v-model="jtem.value" placeholder="" />
                  </el-checkbox>
                </el-checkbox-group>
              </td>
            </tr>

          </table>

        </div>
      </el-card>
    </div>
  </div>
</template>
  
<script>
const PMList = [
  ",,,,,,,,,,,,,,/,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,",
  ",,,,,/,,,,,,,,,,,,,,,,,,,,,,,,,,,,,",
];
const listHeader = [
  "11",
  "12",
];

const captial = [
  "11",
  "12",
];
export default {
  name: "Material",
  components: {},
  created() {

    for (let i = 0; i < PMList.length; i++) {
      let list = PMList[i].split(",");
      this.checkedOpt.push(list);
      let nodes = [];
      this.data.push({
        label: captial[i],
        children: []
      });
      for (let m = 0; m < list.length; m++) {
        nodes.push({
          label: list[m],
          value: 0,
          children: []
        });
      }
      this.data[i].children.push({
        label: listHeader[i],
        children: nodes
      })
    }
  },
  watch: {},
  computed: {
  },
  data() {

    return {
      id: this.$route.query.id,
      checkAll: [
        false,
        false,
      ],
      checkedList: [[], []],
      checkedOpt: [],
      data: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
    };
  },

  methods: {
    handleCheckAllChange(index) {
      this.checkedList[index] = [];
      if (this.checkAll[index]) {
        for (let i = 0; i < this.checkedOpt[index].length; i++) {
          this.checkedList[index].push(i);
        }
      }
    },
    handlePM() {
      let cheakList = this.checkedList;
      let data = "";
      let temp = "";

      for (let i = 0; i < cheakList.length; i++) {
        if (cheakList[i].length == 0) continue;
        for (let j = 0; j < cheakList[i].length; j++) {
          if (i == 0 && j == 0) {
            temp += this.data[i].children[0].children[cheakList[i][j]].label + "=" + this.data[i].children[0].children[cheakList[i][j]].value;

          } else {
            temp += "|" + this.data[i].children[0].children[cheakList[i][j]].label + "=" + this.data[i].children[0].children[cheakList[i][j]].value;

          }
        }
      }
      data = temp + '|' + '';

      const pro = []
      // let targetNum = "";
      for (let i = 0; i < cheakList.length; i++) {
        for (let j = 0; j < cheakList[i].length; j++) {
          pro.push({
            "materialName": this.data[i].children[0].children[cheakList[i][j]].label, //
            "total": this.data[i].children[0].children[cheakList[i][j]].value, //
            "doneCount": 0, //
            "activePowerCost": 0, //
            "status": "N", //  N D E
            "createTime": Date.now() + 8 * 60 * 60 * 1000, // long 
            "lastUpdateTime": ""
          })
          // if(i == 0 && j == 0) {
          //   targetNum += this.data[i].children[0].children[cheakList[i][j]].value;
            
          // }
          // targetNum += '|' + this.data[i].children[0].children[cheakList[i][j]].value;
        }
      }
      const params = {
        orderId: this.id,
        boostingTask: data,
        operateType: "ADD",
        progress: JSON.stringify(pro),
        target: data,

        businessType: "GatherMaterials"
      };
      this.$api.taskBoosting(params).then((res) => {
        const body = res.data;
        if (body.code != "200") {
          this.$message.success("");
        } else {
          this.$message.error(body.message);
        }
      });
    },
  },
};
</script>

