<template>
  <div id="app" @click="updateLastTime()">
    <router-view/>
  </div>
</template>
<script>
  export default {
    name: 'App',
    created() {
      //   
      this.isLoginOut();
    },
    methods: {
      updateLastTime(){
        this.$store.commit('setLastTime'
          , new Date().getTime());
      },
      // 
      isTimeOut(){
        // 
        this.lastTime = this.$store.state.lastTime;
        this.currentTime = new Date().getTime();
        // 
        if((this.currentTime - this.lastTime) > this.sys_timeout) {
          return true;
        } else {
          return false;
        }
      },

      isLoginOut() {
        //   
        clearInterval(this.whole_timer);
        // this
        let _this = this;
        this.whole_timer = setInterval(function () {
          if (_this.isTimeOut()) {
            _this.$message.error("");

            _this.$store.commit('delToken');
            _this.$router.push(`/login`);
            setTimeout(function() {
              clearInterval(_this.whole_timer);
            }, 1000);
          }
        }, _this.check_time);
      }


    },
    data() {
      return {
        // 
        lastTime: null,
        // 
        currentTime: null,
        // , 
        sys_timeout: 20 * 60* 1000,
        // 5
        check_time: 5 * 60 * 1000,
        // 
        whole_timer: null

      }
    }
  }
</script>

