<template>
  <div class="Skills">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <div class="content-inner">
        <!--  -->
        <div class="list">
          <el-table stripe border size="mini" :data="tableData" style="width: 100%" :max-height="tableMaxHeight">
            <el-table-column v-for="(item, i) in tableHeader" :prop="item.key" :label="item.name" :key="i">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.fullBreakClass"></el-checkbox>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.fullBreakSkill"></el-checkbox>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.grailCheck">:</el-checkbox>
                <el-input size="mini" v-model="scope.row.grailNum" autocomplete="off">100</el-input>

              </template>
            </el-table-column>
            <el-table-column fixed="right" label="" width="80">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="handleAdd(scope.row)">
                </el-button>

              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="content-footer">
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Skills",
  components: {},
  created() {
    if (window.innerHeight < 900) {
      this.tableMaxHeight = "500px";
    } else if (window.innerHeight >= 900) {
      this.tableMaxHeight = "700px";
    }
    this.handleUpdate();
  },
  watch: {},
  data() {
    return {
      id: this.$route.query.id,

      tableMaxHeight: "",
      tableHeader: [
        {
          key: "career",
          name: "",
        },
        {
          key: "name",
          name: "",
        },
        {
          key: "star",
          name: "",
        },
        {
          key: "come",
          name: "",
        },
        {
          key: "class",
          name: "",
        },
        {
          key: "skill1",
          name: "1",
        },
        {
          key: "skill2",
          name: "2",
        },
        {
          key: "skill3",
          name: "3",
        }
      ],
      tableData: [],
    };
  },

  methods: {
    handleUpdate() {
      const params = {
        orderId: this.id,
      };
      this.$api.getInfoFollower(params).then(
        (res) => {
          const body = res.data;
          if (res.code == "200") {
            this.tableData = [];
            for (let i = 0; i < body.follower.length; i++) {
              let f = body.follower[i];
              this.tableData.push({
                career: f.profession,
                name: f.followerName,
                star: f.starLevel,
                come: f.comeAgainLevel,
                class: f.followerLevel,
                skill1: f.skill1Level,
                skill2: f.skill2Level,
                skill3: f.skill3Level,
                fullBreakClass: f.levelBreak,
                fullBreakSkill: f.skillBreak,
                grailCheck: f.holyGrailChange,
                grailNum: f.holyGrailTotal,
              })
            }
            if(res.message == null || res.message == "") {
              this.$message.success("");
            } else {
              this.$message.success(res.message);
            }

          } else {
            this.$message.error(body.message);
          }
        }

      );
    },
    /**
     * 
     */
    handleAdd(row) {

      let data = "=" + row.career +
        "|=" + row.name +
        "|=" + row.star +
        "|=" + row.come +
        "|=" + row.class +
        "|1=" + row.skill1 +
        "|2=" + row.skill2 +
        "|3=" + row.skill3 +
        "|=" + row.fullBreakClass +
        "|=" + row.fullBreakSkill +
        "|=" + row.grailNum;
      const pro = [{
        "profession": row.career,
        "followerName": row.name,
        "starLevel": row.star,
        "comeAgainLevel": row.come, //
        "followerLevel": row.class, //
        "skill1Level": row.skill1, //1
        "skill2Level": row.skill2,
        "skill3Level": row.skill3,
        "levelBreak": row.fullBreakClass, //
        "skillBreak": row.fullBreakSkill, //
        "holyGrailChange": row.grailCheck, //
        "holyGrailTotal": row.grailNum, //
        "status": "N", //  N D E
        "createTime": Date.now() + 8 * 60 * 60 * 1000, // long 
        "lastUpdateTime": ""
      }]
      const params = {
        orderId: this.id,
        boostingTask: data + '|' + "",
        operateType: "ADD",
        progress: JSON.stringify(pro),
        target: row.fullBreakClass + '|' + row.fullBreakSkill + '|' + row.grailCheck + '|' + row.grailNum,

        businessType: "Follower"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.$message.success("");

          } else {
            this.$message.error(body.message);
          }
        }

      );



    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}

.content {
  padding: 0px 100px 0 20px;

  .ruleForm {
    width: 300px;
  }
}
</style>
  